import action from '@/components/shufei-action/action.vue';
export default {
	components: { action },
	data() {
		return {
			userInfo: {},
			isTrue:false
		};
	},
	methods: {
		ontrueGetList() {
			// uni.showToast({
			// 	title: '个人中心'
			// });
			console.log('加载了个人中心，可以把网络请求放这里');
			
			this.getMsg()
		},
		//获取个人中心基本信息
		getMsg(){
			if(this.isTrue){
				return
			}
			console.log("个人数据")
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-grzxsj.htm')
				.then(res => {
					
					console.log(res);
					if (res.data.msgState == 1) {
						var userInfo=res.data.list[0]
						this.userInfo=userInfo
						if(this.userInfo.avatar.length<10){
						this.userInfo.avatar="http://fc-feed.cdn.bcebos.com/0/pic/9107b498a0cbea000842763091e833b6.jpg"	
						}
						
					}
					
				})
				.catch(err => {
					console.log(err);
					
				});
		},
		//修改头像
		changeAvatar(e) {
			this.$refs.action.actionConfig = {
				 // title: '选择',
				list: ['预览头像', '拍摄照片', '从相册选择'],
				type: 0,
				isBorderColor: false,
				specClass: 'show'
			};
		},
		itemClick(index, type) {
			this.isTrue=true
			//这里根据不同的类型点击的索引值,做对应的逻辑处理
			console.log(`你点击的action-sheet的类型是${type},list对应的索引值是${index}`);
			var _this = this;
			var sourceType = ['album'];
			switch (index) {
				case 0:
					var imglist = [];
					imglist.push(this.userInfo.avatar);
					uni.previewImage({
						urls:imglist
					});
					break;
				case 1:
					sourceType = ['camera'];
					uni.chooseImage({
						count: 1, //默认9
						sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
						sourceType: ['camera'], //拍照
						success: function(res) {
							_this.chooseImage1(res.tempFilePaths[0]) ;
							// console.log(JSON.stringify(res.tempFilePaths[0]));
						}
					});
					break;
				case 2:
					uni.chooseImage({
						count: 1, //默认9
						sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
						sourceType: ['album'], //从相册选择
						success: function(res) {
							_this.chooseImage1(res.tempFilePaths[0]) ;
							//_this.userInfo.img = res.tempFilePaths[0];
							//console.log(JSON.stringify(res.tempFilePaths[0]));
						}
					});
					break;
				default:
					break;
			}
		},
		//上传头像
		chooseImage1(val){
			uni.showLoading({
				title: '上传中...',
				mask: true
			})
			uni.uploadFile({
				url: 'https://app8848.com/interface/rest/file/upload/util/uploadFile.htm', //仅为示例，非真实的接口地址
				 header:{token:"311288512_"},
				filePath: val,
				name: 'file',
				
				success: (uploadFileRes) => {
					// console.log("图片上传+++++++")
					// console.log(JSON.parse(uploadFileRes.data));
					
					var datas=JSON.parse(uploadFileRes.data)
					if(datas.msgState==1){
						this.isTrue=true
						//将返回的数据存入img,并通知外界
						// 默认返回的有路径，字段名 url
						this.updata(datas.result);
						return datas.result;
						
						
					}else{
						this.isTrue=false
						uni.showToast({
						    title: '上传失败，请重新上传',
						    duration: 2000
						});
					
					}
					
				}
			});
			
			},
		updata(val){
			var obj={avatar:val}
			var that=this
			this.$http
				.post('/interface/rest/http/xlwb/xlgc-wb-xcx-grzx-xgtx.htm', obj)
				.then(res => {
					console.log('图像上传');
					console.log(res);
					if (res.data.msgState == 1) {
						this.userInfo.avatar=val
					}
					uni.showToast({
						title: res.data.msg,
						duration: 2000
					});
				})
				.catch(err => {
					console.log(err);
			
				});
		},
		titleClick(type) {
			//这里根据不同的类型做对应的逻辑处理
			console.log(type);
		},
		//修改手机号
		changePhone() {
			let data = {
				phone: this.userInfo.phone
			};
			let url = 'change-phone';

			uni.navigateTo({
				url: `/pages/change-phone/${url}?data=${JSON.stringify(data)}`
			});
		},
		loginOuts() {
			this.$refs.showtip.open();
		},
		cancel(type) {
			this.$refs.showtip.close();
		},
		//退出登陆
		loginOut() {
			uni.redirectTo({
				url: `/pages/login/login`
			});
		},
		//我的上报
		myUpdata(){
			uni.navigateTo({
				url: `/pages/my-report/my-report`
			});
		}
	}
};
